import { Alert, Descriptions, Typography } from "antd";
import Links, { LinkKey } from "../../Data/Links";

function Link({ name, description }: { name: LinkKey, description?: string }) {
    return <li>
        <a href={Links.get(name)} target="_blank" rel="noreferrer" >{name}</a>
        {description !== undefined ? "" + description + "" : ""}
    </li>
}

const MenuItem = {
    label: <Typography.Text type="success"></Typography.Text>,
    key: '/Discover/LinksUI',
    page: () => <Typography.Paragraph>
        <Descriptions bordered labelStyle={{ width: "200px" }} column={1}>
            <Descriptions.Item label="">
                <ul>
                    <Link name="KISS" />
                    <Link name="COM3D2" />
                    <Link name="COM3D2" />
                    <Link name="COM3D2" />
                </ul>
            </Descriptions.Item>
            <Descriptions.Item label="">
                <ul>
                    <Link name="COM3D2SeesaaWiki"></Link>
                    <Link name="COM3D2SeesaaWiki_Mod"></Link>
                    <Link name="COM3D2Wiki"></Link>
                    <Link name="COM3D2"></Link>
                </ul>
            </Descriptions.Item>
            <Descriptions.Item label="">
                <ul>
                    <Link name="COM3D2_Discord"></Link>
                    <Link name="COM3D2_Reddit"></Link>
                </ul>
                <Alert type="success" showIcon message="Mod"></Alert>
            </Descriptions.Item>
            <Descriptions.Item label="">
                <ul>
                    <Link name="GGBases" />
                    <Link name="Sukebei" />
                    <Link name="COM3D2.5" description="" />
                </ul>
            </Descriptions.Item>
            <Descriptions.Item label="">
                <ul>
                    <Link name="COM3D2" />
                </ul>
            </Descriptions.Item>
            <Descriptions.Item label={<div>
                Mod
            </div>}>
                <ul>
                    <Link name="COM3D2Mod_Mukuu" />
                    <Link name="COM3D2Mod_Motimoti3d" />
                    <Link name="COM3D2Mod_3DM" />
                </ul>
                <Alert type="success" showIcon message="ModModGMod"></Alert>
            </Descriptions.Item>
        </Descriptions>
    </Typography.Paragraph>
}
export default MenuItem;