import { Alert, Button, Card, List, Space } from "antd";
import React from "react";
import { ButtonLink } from "../../Data/Components";

const MenuItem = {
    label: "",
    key: '/Discover/Translator',
    page: () => {
        const texts1 = [
            {
                title: "V1",
                extra: <Button disabled></Button>,
                content: <ul>
                    <li></li>
                    <li></li>
                </ul>
            },
            {
                title: "V2",
                extra: <ButtonLink href="https://wwzc.lanzouj.com/idml10uwhute" ></ButtonLink>,
                content:
                    <div>
                        
                        <ul>
                            <li><a href="https://github.com/BDFFZI/XUnity.AutoTranslator" target="_blank" rel="noreferrer">XUnity.AutoTranslator</a></li>
                        </ul>
                        
                        <ul>
                            <li>V1</li>
                            <li></li>
                        </ul>
                        
                        <ul>
                            <li></li>
                            <li></li>
                            <li></li>
                        </ul>
                        <br />
                        <ButtonLink type="link" href="https://wwzc.lanzouj.com/idml10uwhute" block></ButtonLink>
                    </div>
            },
            {
                title: "V3.2 COM3D2 2.28",
                extra: <ButtonLink href="https://wwzc.lanzouj.com/i1U2R13t4ztc" ></ButtonLink>,
                content:
                    <Space direction="vertical">
                        <div>
                            
                            <ul>
                                <li>YATranslator(<a href='https://github.com/BDFFZI/CM3D2.YATranslator.git' target="_blank" rel="noreferrer">BDFFZI.YATranslator</a>)</li>
                                <li>(Bupzaj2001)</li>
                            </ul>
                        </div>
                        <div>
                            
                            <ul>
                                <li>v3-</li>
                                <li>v2v2</li>
                                <li></li>
                                <li></li>
                            </ul>
                        </div>
                        <div>
                            
                            <ul>
                                <li></li>
                                <li></li>
                            </ul>
                        </div>
                        <div>
                            
                            <br />CMI
                            <br />v2v2
                        </div>
                        <Alert showIcon type="warning" message="V3" description={
                            <ul>
                                <li><br />v2</li>
                                <li><br />Sybaris\UnityInjector\Config\Assets</li>
                            </ul>
                        }
                        ></Alert>
                    </Space >
            },
            {
                title: "V4",
                extra: <Button disabled></Button>,
                content: <div>
                    
                    <ul>
                        <li><a href="https://github.com/ghorsington/COM3D2.i18nEx" target="_blank" rel="noreferrer">COM3D2.i18nEx</a></li>
                        <li>AI</li>
                    </ul>
                    
                    <ul>
                        <li></li>
                        <li></li>
                    </ul>
                </div>
            }
        ]


        return <div>
            <Alert showIcon
                type="error"
                description="CMI"
            />
            <List
                dataSource={texts1}
                renderItem={(item, index) =>
                    <List.Item key={index}>
                        <Card
                            title={item.title}
                            extra={item.extra}
                            style={{ width: 600 }}
                        >
                            {item.content}
                        </Card>


                    </List.Item>

                }
            >
            </List>
        </div>
    }
}
export default MenuItem;