import { Typography, Alert, Space, Card, Button, Divider } from 'antd'
import { Link } from 'react-router-dom';
import Module from './Module'
import Links from '../../Data/Links';
import { ButtonLink } from '../../Data/Components';

const MenuItem = {
    label: "2. ",
    key: "/Introduction/Plugin",
    page: () => <div>
        <Typography.Title level={2}></Typography.Title>
        <Typography.Paragraph>
            
        </Typography.Paragraph>
        <Typography.Title level={2}></Typography.Title>
        <Typography.Paragraph>
            <Space direction='vertical'>
                <div>
                    COM-Modular-Installer(CMI)
                    
                    <br />
                    
                </div>
                <Card title="CMI ">
                    <Space>
                        <ButtonLink href={Links.get("COM3D2_")}></ButtonLink>
                        <ButtonLink href="https://mod.3dmgame.com/mod/186198">3DM</ButtonLink>
                        <Button disabled></Button>
                    </Space>
                </Card>
                <Alert showIcon message="" type="error" />
                <Alert showIcon type='warning' message="(CMI)"></Alert>
                <Alert type="success" message="" description={
                    <ul>
                        <li>NPRShaderMod</li>
                        <li>CMI</li>
                        <li><strong>CMI Documentation</strong></li>
                        <li>CMI</li>
                        <li>CMI</li>
                        <li>ModCMIMod</li>
                    </ul>
                } />
            </Space>
        </Typography.Paragraph>
        <Typography.Title level={2}></Typography.Title>
        <Typography.Paragraph>
            <Space direction='vertical'>
                CMI-
                <Alert showIcon type="warning" message="v3CMI"></Alert>
            </Space>
        </Typography.Paragraph>
        <Divider></Divider>
        <Alert showIcon type="success" message={
            <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
                Mod
                <Button><Link to={Module.key}></Link> </Button>
            </div>
        }></Alert>
    </div >
}

export default MenuItem;