import { Alert, Button, Divider, List, Typography } from "antd";
import { ButtonLink } from "../../Data/Components";
import Links from "../../Data/Links";
import { ReactNode } from "react";

const faq: { que: string, ans: ReactNode }[] = [
    {
        que: "",
        ans: "()"
    },
    {
        que: "",
        ans: <ul>
            <li>NPRShaderCMI</li>
            <li></li>
            <li>Mod</li>
        </ul>
    },
]

const MenuItem = {
    label: <Typography.Text type="danger"></Typography.Text>,
    key: "/Introduction/FAQ",
    page: () => <div>
        <Alert
            showIcon
            type="warning"
            message="WIKIWIKI"
            description={<ButtonLink
                style={{ maxWidth: "500px" }}
                href={Links.get("COM3D2SeesaaWiki")}>
                COM3D2 WIKI
            </ButtonLink>}
        />
        <br></br>
        <Alert
            showIcon
            type="warning"
            message="CMI"
            description={
                <Button
                    ghost
                    type="primary"
                    style={{ maxWidth: "500px", pointerEvents: "none" }}>
                    CMI Documentation
                </Button>}
        />
        <Divider></Divider>
        <List
            dataSource={faq}
            renderItem={(item, index) =>
                <List.Item key={index}>
                    <List.Item.Meta
                        title={item.que}
                        description={item.ans}
                    />
                </List.Item>
            }
        />
    </div >
}
export default MenuItem;